import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-hipaa-warning',
  templateUrl: './hipaa-warning.component.html',
  styleUrls: ['./hipaa-warning.component.css']
})
export class HipaaWarningComponent implements OnInit {
  @Output() warningAccepted = new EventEmitter<boolean>();
  constructor() { }

  ngOnInit(): void {
  }

  onAccept(): void {
    this.warningAccepted.emit(true);
  }
}
