import {Component, OnInit, Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'app-government-warning',
  templateUrl: './government-warning.component.html',
  styleUrls: ['./government-warning.component.css']
})
export class GovernmentWarningComponent implements OnInit {
  @Output() warningAccepted = new EventEmitter<boolean>();
  constructor() { }

  ngOnInit(): void {
  }

  onAccept(): void {
    this.warningAccepted.emit(true);
  }
}
