import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'XploSafe';
  userAcceptedGovernmentWarning = true;
  userAcceptedHIPAAWarning = false;
  userLoggedIn = false;

  onAcceptGovtWarning(): void {
    this.userAcceptedGovernmentWarning = true;
  }

  onAcceptHIPAAWarning(): void {
    this.userAcceptedHIPAAWarning = true;
  }

  onLoginPressed($event: boolean): void {
    this.userLoggedIn = $event;
  }

  getUserAcceptedHIPAAWarning(): boolean {
    return this.userAcceptedHIPAAWarning === true;
  }

  getUserAcceptedGovernmentWarning(): boolean {
    return this.userAcceptedGovernmentWarning === true;
  }

  getUserLoggedIn(): boolean {
    return this.userLoggedIn === true;
  }
}
